html,
body {
  height: 100%;
}
.navbar-default {
  font-weight: bold;
}
.navbar-toggle {
  padding: 10px 10px;
}
.navbar-brand {
  padding: 15px 15px;
}
.navbar-brand > img {
  height: 100%;
}
.navbar-lead {
  min-height: 50px;
  color: #fff;
  font-weight: normal;
  padding: 15px 15px;
  background-color: #337ab7;
}
.dropdown-menu {
  border-top: none;
}
.warning {
  background-color: #f0ad4e;
}
.danger {
  background-color: #d9534f;
}
.date {
  font-size: 0.8em;
  font-style: italic;
}
#wrap {
  min-height: 100%;
  height: auto !important;
  height: 100%;
}
